import {
  AfterContentChecked,
  ChangeDetectionStrategy,
  ChangeDetectorRef,
  Component,
  ElementRef,
  EventEmitter,
  Input,
  OnInit,
  Output,
  Renderer2,
  ViewEncapsulation
} from '@angular/core';

import { toNumber, NzUpdateHostClassService } from 'ng-zorro-antd/core';
import {TabChangeEvent} from "../cmacs-tabs/cmacs-tabset.component";
import {CmacsTabComponent} from "../cmacs-tabs/cmacs-tab.component";
import {getISOWeeksInYear} from "date-fns";
import {getISOWeek} from "date-fns";

@Component({
  selector: 'cmacs-timeline-datepicker',
  exportAs: 'cmacsTimelineDatepicker',
  templateUrl: './cmacs-timeline-datepicker.component.html',
  changeDetection: ChangeDetectionStrategy.OnPush,
  encapsulation: ViewEncapsulation.None,
  preserveWhitespaces: false,
  providers: [NzUpdateHostClassService],
  styleUrls: ['./cmacs-timeline-datepicker.component.css'],
  styles: [
    `
      cmacs-timeline-datepicker {
        display: block;
        border-radius: 3px;
        box-shadow: 0 3px 7px 0 rgba(59, 63, 70, 0.15);
        background-color: #ffffff;
        padding: 7px 0 4px 0 !important;
      }
    `
  ]
})
export class CmacsTimelineDatepickerComponent implements AfterContentChecked, OnInit {
  private indexToSelect: number | null = 0;
  private el: HTMLElement = this.elementRef.nativeElement;
  private _selectedIndex: number | null = null;
  private _date: Date | null = new Date();
  listOfNzTabComponent: any[];

  @Input() gutter: number = 2;
  @Input() mode: 'week' | 'quarter' | 'month' = 'week';
  @Output() readonly onNextClick = new EventEmitter<void>();
  @Output() readonly onPrevClick = new EventEmitter<void>();

  @Output() readonly selectChange: EventEmitter<TabChangeEvent> = new EventEmitter<TabChangeEvent>(true);
  @Output() readonly selectedIndexChange: EventEmitter<number> = new EventEmitter<number>();
  @Output() readonly dateChange: EventEmitter<Date> = new EventEmitter<Date>();
  modeOptions = [ {title: 'Week', selected: true}, {title: 'Month', selected: false}, {title: 'Quarter', selected: false} ];

  @Input()
  set selectedIndex(value: number | null) {
    this.indexToSelect = value ? toNumber(value, null) : null;
  }

  get selectedIndex(): number | null {
    return this._selectedIndex;
  }

  @Input()
  set date(value: Date | null) {
    this._date = value;
    this.selectedIndex = getISOWeek(value) - 1;
  }

  get date(): Date | null {
    return this._date;
  }

  getWeek(result: Date): void {
    this.date = result;
    this.dateChange.emit(result);
    console.log('weeks:  ', getISOWeek(result));
  }

  setClassMap(): void {
    this.nzUpdateHostClassService.updateHostClass(this.el, {
      [`ant-tabs`]: true,
      [`ant-tabs-line`]: true
    });
  }

  clickLabel(index: number, disabled: boolean): void {
    if (!disabled) {
      this.selectedIndex = index;
      //this.listOfNzTabComponent[index].click.emit();
    }
  }

  createChangeEvent(index: number): TabChangeEvent {
    const event = new TabChangeEvent();
    event.index = index;
    if (this.listOfNzTabComponent && this.listOfNzTabComponent.length) {
      event.tab = this.listOfNzTabComponent[index];
      this.listOfNzTabComponent.forEach((item, i) => {
        if (i !== index) {
          item.deselect.emit();
        }
      });
      event.tab.select.emit();
    }
    return event;
  }

  /** Clamps the given index to the bounds of 0 and the tabs length. */
  private clampTabIndex(index: number | null): number {
    // Note the `|| 0`, which ensures that values like NaN can't get through
    // and which would otherwise throw the component into an infinite loop
    // (since Math.max(NaN, 0) === NaN).
    return Math.min(this.listOfNzTabComponent.length - 1, Math.max(index || 0, 0));
  }

  constructor(
    private renderer: Renderer2,
    private nzUpdateHostClassService: NzUpdateHostClassService,
    private elementRef: ElementRef,
    private cdr: ChangeDetectorRef
  ) {}

  ngOnInit(): void {
    this.setClassMap();
    this.listOfNzTabComponent = this.getValuesSlider();
  }

  getValuesSlider() {
    let temp = [];
    if (this.mode === 'week') {
      const length = getISOWeeksInYear(this.date);
      for(let i = 0; i <= length; i++) {
        temp.push({title: this.formatWeekNumber(i + 1)});
      }
      return temp;
    } else if (this.mode === 'month') {
      return [{title: 'Jan'}, {title: 'Feb'}, {title: 'Mar'}, {title: 'Apr'}, {title: 'May'}, {title: 'Jun'},
        {title: 'Jul'}, {title: 'Aug'}, {title: 'Sep'}, {title: 'Oct'}, {title: 'Nov'}, {title: 'Dec'}];
    }
  }

  formatWeekNumber(value: number) {
    return ("0" + value).slice(-2);
  }

  ngAfterContentChecked(): void {
    //this.updateSlider();
    if (this.listOfNzTabComponent && this.listOfNzTabComponent.length) {
      // Don't clamp the `indexToSelect` immediately in the setter because it can happen that
      // the amount of tabs changes before the actual change detection runs.
      const indexToSelect = (this.indexToSelect = this.clampTabIndex(this.indexToSelect));
      // If there is a change in selected index, emit a change event. Should not trigger if
      // the selected index has not yet been initialized.
      if (this._selectedIndex !== indexToSelect) {
        const isFirstRun = this._selectedIndex == null;
        /*if (!isFirstRun) {
          this.selectChange.emit(this.createChangeEvent(indexToSelect));
        }*/

        // Changing these values after change detection has run
        // since the checked content may contain references to them.
        Promise.resolve().then(() => {
          this.listOfNzTabComponent.forEach((tab, index) => (tab.isActive = index === indexToSelect));

          if (!isFirstRun) {
            this.selectedIndexChange.emit(indexToSelect);
          }
        });
      }

      // Setup the position for each tab and optionally setup an origin on the next selected tab.
      this.listOfNzTabComponent.forEach((tab: CmacsTabComponent, index: number) => {
        tab.position = index - indexToSelect;

        // If there is already a selected tab, then set up an origin for the next selected tab
        // if it doesn't have one already.
        if (this._selectedIndex != null && tab.position === 0 && !tab.origin) {
          tab.origin = indexToSelect - this._selectedIndex;
        }
      });

      if (this._selectedIndex !== indexToSelect) {
        this._selectedIndex = indexToSelect;
        this.cdr.markForCheck();
      }
    }
  }

  customSelect(index: number) {
    this.modeOptions.forEach((option) => {
      option.selected = false;
    });
    this.modeOptions[index].selected = true;
  }

  getSelected(){
    return this.modeOptions.filter(item => item.selected);
  }

}




